<i18n>
{
  "en": {
    "Feedback": "Feedback",
    "Product": "Product",
    "Charger": "Charger",
    "Battery": "Battery",
    "ProblemClassification": "Problem classification",
    "Scan": "Scan",
    "other": "other",
    "borrow": "borrow",
    "repay": "repay",
    "ProblemDescription": "Problem description",
    "inputFeedback": "Please input feedback",
    "UploadPhotos": "Upload photos（3）",
    "optional": "（optional）",
    "ContactNumber": "Contact number",
    "Submit": "Submit"
  },
  "zh": {
    "Feedback": "意见反馈",
    "Product": "产品",
    "Charger": "座充",
    "Battery": "充电宝",
    "ProblemClassification": "问题分类",
    "Scan": "扫码充电",
    "other": "其他",
    "borrow": "借充电宝",
    "repay": "还充电宝",
    "ProblemDescription": "问题描述",
    "inputFeedback": "请输入反馈内容",
    "UploadPhotos": "添加照片（小于3张)",
    "optional": "（可选）",
    "ContactNumber": "联系电话",
    "Submit": "提交"
  }
}
</i18n>

<template>
  <div class="feedback">
    <head-top :title='$t("Feedback")' :go-back="true" @goBackHander="goBackHander" style="zIndex:2"></head-top>
    <div class="container">
      <div class="wrapper" :style="{marginTop: -offsetTop + 'px'}">
        <div class="list">
          <strong>{{ $t("Product") }}
            <span>*</span>
          </strong>
          <div class="list_li" :class="{navBg: navActive===1, cwhite: Version === 2 && navActive===1}" @click="navHandle(1)">{{ $t("Charger") }}</div>
          <div class="list_li" :class="{navBg: navActive===2, cwhite: Version === 2 && navActive===2}" @click="navHandle(2)">{{ $t("Battery") }}</div>
        </div>
        <div class="list" v-if="navActive===1">
          <strong>{{ $t("ProblemClassification") }}
            <span>*</span>
          </strong>
          <div class="list_li" :class="{navBg: itemActive===3, cwhite: Version === 2 &&itemActive===3}" @click="navHandle(3)">{{ $t("Scan") }}</div>
          <div class="list_li" :class="{navBg: itemActive===4, cwhite: Version === 2 && itemActive===4}" @click="navHandle(4)">{{ $t("other") }}</div>
        </div>
        <div class="list" v-if="navActive===2">
          <strong>{{ $t("ProblemClassification") }}
            <span>*</span>
          </strong>
          <div class="list_li" :class="{navBg: itemActive===5, cwhite: Version === 2 && itemActive===5}" @click="navHandle(5)">{{ $t("borrow") }}</div>
          <div class="list_li" :class="{navBg: itemActive===6, cwhite: Version === 2 && itemActive===6}" @click="navHandle(6)">{{ $t("repay") }}</div>
          <div class="list_li" :class="{navBg: itemActive===7, cwhite: Version === 2 && itemActive===7}" @click="navHandle(7)">{{ $t("other") }}</div>
        </div>
        <div class="problem">
          <strong>{{ $t("ProblemDescription") }}
            <span>*</span>
          </strong>
          <textarea @focus="focus('textarea')" maxlength="200" :placeholder=" $t('inputFeedback')"></textarea>
        </div>
        <div class="picture">
          <strong>{{ $t("UploadPhotos") }}
            <span>{{ $t("optional") }}</span>
          </strong>
          <div class="pic-list">
            <div class="pic-li">
              <img src="../../assets/feedback_images/picture.jpg" alt="">
            </div>
            <div class="pic-li">
              <img src="../../assets/feedback_images/addition.png" alt="">
            </div>
          </div>
        </div>
        <div class="phone">
          <input @focus="focus('input')" type="number" oninput="if(value.length>11)value=value.slice(0,11)">
          <strong>
            {{ $t("ContactNumber") }}<span>{{ $t("optional") }}</span>
          </strong>
        </div>
        <button @click="goHandle" v-show="!keyboard" class="btn">{{ $t("Submit") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'feedback',
  data () {
    return {
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      focusType: 'input', // 获取焦点类型
      navActive: 1, // 产品类型
      itemActive: 0 // 问题分类
    }
  },

  components: {
    headTop
  },

  mounted () {
    let h = 0
    this.fullHeight = document.documentElement.clientHeight
    window.onresize = () => {
      h = document.documentElement.clientHeight
      // 软键盘是否弹起
      if (h < this.fullHeight) {
        this.keyboard = true
      } else {
        this.keyboard = false
      }
      // 解决小屏幕遮挡输入框
      let top
      if (this.focusType === 'input') {
        top = 520
      } else if (this.focusType === 'textarea') {
        top = 360
      }
      if (h < top) {
        this.offsetTop = top - h
      } else {
        this.offsetTop = 0
      }
    }
  },

  methods: {
    focus (type) {
      this.focusType = type
    },

    goBackHander () {
      // console.log(123)
    },

    navHandle (num) {
      switch (num) {
        case 1:case 2:
          this.navActive = num
          break
        case 3:case 4:case 5:case 6:case 7:
          this.itemActive = num
          break
      }
    },

    goHandle () {
      if (this.ISDEBUG) {
        this.$http('/scanning/1').then(res => {
          if (res.data.result) {
            this.$router.push('/feedbackSuccess')
          } else {
            this.$router.push('/feedbackFailure')
          }
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.feedback{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    background: #FFF;
    padding: 20px 30px;
    .wrapper{
      .wh(100%, 100%);
      padding-bottom: 150px;
      .list{
        .wh(100%, 160px);
        font-size: 26px;
        strong{
          .wh-l(100%, 50px);
          font-size: 30px;
          display: block;
          text-align: left;
          margin-bottom: 20px;
          span{
            color: #F45143;
          }
        }
        .list_li{
          .wh-l(200px, 60px);
          border-radius: 30px;
          background: #F6F6F6;
          display: block;
          float: left;
          margin-right: 25px;
        }
        .navBg{
          background: @themeColor;
        }
        .cwhite{
          color: #FFF;
        }
      }
      .problem{
        strong{
          .wh-l(100%, 50px);
          font-size: 30px;
          display: block;
          text-align: left;
          margin-bottom: 20px;
          span{
            color: #F45143;
          }
        }
        textarea{
          .wh(100%, 200px);
          border-radius: 10px;
          border: 2px solid #bfbfbf;
          padding: 20px;
          resize: none;
          font-size: 26px;
        }
      }
      .picture{
        .wh(100%, 230px);
        margin-top: 10px;
        strong{
          .wh-l(100%, 50px);
          font-size: 30px;
          display: block;
          text-align: left;
          span{
            font-size: 26px;
            .zoom-font(0.7);
            display: inline-block;
          }
        }
        .pic-list{
          .wh(100%, 130px);
          margin-top: 20px;
          .pic-li{
            .wh(130px, 130px);
            float: left;
            margin-right: 70px;
            img{
              height: 100%;
            }
          }
        }
      }
      .phone{
        .wh-l(100%, 60px);
        position: relative;
        margin-top: 30px;
        strong{
          .wh(100%, 100%);
          font-size: 30px;
          display: block;
          text-align: left;
          position: absolute;
          left: 0;
          top: 0;
          span{
            font-size: 26px;
            .zoom-font(0.7);
            display: inline-block;
          }
        }
        input{
          .wh(100%, 100%);
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          border-bottom: 1px solid #CCCCCC;
          text-align: right;
          color: #999999;
        }
      }
      button{
        .wh(690px, 100px);
        background: @themeColor;
        border-radius: 10px;
        font-size: 30px;
        position: absolute;
        left: 50%;
        bottom: 50px;
        margin-left: -345px;
        box-shadow: 0px 4px 25px @themeColor;
        color: #5A5A57;
      }
    }
  }
}
</style>



